import axios from 'axios';

const url = 'api/pages/';

const axiosInstance = axios.create({
    baseURL: process.env.BASE_URL
});


class PageService {

    static getRoutes() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axiosInstance.get(url);
                const data = res.data;
                resolve(
                    data.map(page => ({
                            ...page
                        })
                    )
                )
            }
            catch(err) {
                reject(err);
            }
        })
    }

    static getOneRoute(identifier) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axiosInstance.get(`${url}${identifier}`);
                const data = res.data;
                resolve(
                    data.map(page => ({
                            ...page
                        })
                    )
                )
            }
            catch(err) {
                reject(err);
            }
        })
    }

    static async addNewPost(data) {

        try {

            let res =  await axiosInstance.post(`${url}`, data);

            return  res;

        } catch (err) {

            console.log(err)
        }

    }
    static async deletePost(id) {

        try {

            let res = await axiosInstance.delete(`${url}${id}`)

            return  res;

        } catch (err) {

            console.log(err)
        }
    }

    static updatePost(id, post) {

        try {

            let res = axiosInstance.put(`${url}${id}`, post)

            return  res;

        } catch (err) {

            console.log(err)
        }
    }



}

export default PageService;
