<template>
    <div>
        <Nav/>
        <div class="dash__container">
            <h1>User Profile</h1>
            <ul class="list-group">
                <li class="list-group-item">Email: {{userInfo.email}}</li>
                <li class="list-group-item">Username: {{userInfo.username}}</li>
                <li class="list-group-item">Name: {{userInfo.name}}</li>
            </ul>
        </div>
    </div>

</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import Nav from './NavBarDash';
    export default {
        name: "Profile",
        data() {
            return {
                userInfo: {
                    email: '',
                    username: '',
                    name: ''
                }
            }
        },
        components: {
            Nav
        },
        computed: {
            ...mapGetters(['user']),
        },
        methods: {
            ...mapActions(['getProfile'])
        },
        async mounted() {
            await this.getProfile();
            this.userInfo.email = this.user[0].email;
            this.userInfo.name = this.user[0].name;
            this.userInfo.username = this.user[0].username;


        }
    }
</script>

