import axios from 'axios';

const url = 'api/restaurants/';

const axiosInstance = axios.create({
    baseURL: process.env.BASE_URL
});

class RestaurantService {

    static getRestaurants() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axiosInstance.get(url);
                const data = res.data;
                resolve(
                    data.map(restaurant => ({
                        ...restaurant,
                            name: restaurant.name
                        })
                    )
                )
            }
            catch(err) {
                reject(err);
            }
        })
    }

    static async getOne(id) {
            try {
                const res = await axiosInstance.get(`${url}${id}`);
                const data = res.data;

                return data

            }
            catch(err) {
                console.log(err)
            }
    }



    static getCloseRestaurants(coord, time) {

        return new Promise(async (resolve, reject) => {
            try {

                const res = await axiosInstance.get(`${url}${coord}/ ${time}`);

                const data = res.data;

                resolve(
                    data.map(restaurant => ({
                            ...restaurant,
                            name: restaurant.name
                        })
                    )
                )
            }
            catch(err) {
                reject(err);
            }
        })
    }

    static async insertRestaurant(data) {

        try {

            let res =  await axiosInstance.post(`${url}`, data);

            return  res;

        } catch (err) {

                console.log(err)
        }

    }


    static async deleteRestaurant(id) {

        try {

            let res = await axiosInstance.delete(`${url}${id}`)

            return  res;

        } catch (err) {

            console.log(err)
        }
    }

    static async updateRestaurant(id, restaurant) {

        try {

            let res = await axiosInstance.put(`${url}${id}`, restaurant)

            return  res;

        } catch (err) {

            console.log(err)
        }
    }
}

export default RestaurantService;
