<template>
  <div>
      <div class="dash__container">
    <h1>Dashboard</h1>
          <ul class="list-group">
              <li class="list-group-item">Visitors</li>
              <li class="list-group-item">Restaurants</li>
              <li class="list-group-item">Messages</li>
              <li class="list-group-item">Pending Approval</li>
          </ul>
      </div>
  </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    export default {
        name: "Dashboard",
        data() {
            return {
                userInfo: {
                    email: '',
                    username: '',
                    name: ''
                }
            }
        },
        computed: {
            ...mapGetters(['user']),
        },
        methods: {
            ...mapActions(['getProfile'])
        },
        async mounted() {
            await this.getProfile();
            this.userInfo.email = this.user.email;
            this.userInfo.name = this.user.name;
            this.userInfo.username = this.user.username;
        }
    }
</script>

