<template>
    <div class="blog_container">
        <Search  @clicked="updateCoord"/>
        <div v-if="Object.keys(page).length"  class="blog_article">
            <div class="img_container">
                <img v-bind:src="require(`../assets/${page.image}`)" v-bind:alt="page.restaurant" width="250px"/>
            </div>

            <h1>{{page.title}}</h1>
            <div class="author_info">
                <img src="../assets/hhImage.png" width="50px"/>
                <h5>{{page.name}}  <br> {{page.date}} • 2 min read</h5>
            </div>
            <div class="blog_content" v-html="page.content">

            </div>
        </div>
    </div>

</template>

<script>
    import Search from './Search.vue';
    import PageService from "../RoutesService"
    export default {
        name: "Blog",
        data() {
            return {
                posts: '',
                restaurants: [],
                allAddresses: [],
                allCoords: [],
                page: {},
                image: '',
            }
        },
        components:{
            Search
        },
        methods: {
            updateCoord (coord) {
                this.restaurants = [];
                this.allAddresses = [];
                this.allCoords = [];
                this.restaurants = coord;
                this.address = coord[0].address;
                coord.forEach(restaurant => {
                    this.allAddresses.push(restaurant.address);
                    this.allCoords.push(restaurant.position);
                });
            },
        },
        props: [
            'identifier',
        ],
        async mounted() {
            const page = await PageService.getOneRoute(this.identifier);
            console.log(page);
            this.page = page[0];
            this.image = '../assets/' + this.page.image;

        }
    }
</script>

