import Vue from 'vue'
import VueCarousel from 'vue-carousel';
import App from './App.vue'
import router from './router'
import VueCtkDateTimePicker from 'vue-ctk-date-time-picker';
import 'vue-ctk-date-time-picker/dist/vue-ctk-date-time-picker.css';
import store from './storage/index'
import axios from 'axios'
import blogComponent from './components/Blog.vue'
import RouteService from "./RoutesService";
require('./assets/css/styles.less')

Vue.component('VueCtkDateTimePicker', VueCtkDateTimePicker);

Vue.use(VueCarousel);

Vue.config.productionTip = false

//setting up default vue's http modules for api calls
Vue.prototype.$http = axios;

//load the token from localStorage
const token = localStorage.getItem('token');

async function getPages () {
    const pages = await RouteService.getRoutes();
    pages.forEach(page => {
        let newRoute = {
            path: `/${page.route}`,
            component: blogComponent,
            name: `/${page.title}`,
            props: {identifier: page.route}
        }
        router.addRoutes([newRoute])

    })
}
getPages();

//if there is a token we will append default axios authorization headers
if(token) {
    Vue.prototype.$http.defaults.headers.common['Authorization'] = token;
}
new Vue({
  render: h => h(App),
    router,
    store
}).$mount('#app')
