<template>
    <div>
    <div class="dash__container">
        <div class="options_container">
            <h1>Manage Blog Posts</h1>
            <button><router-link to="new-posts">Add Post</router-link></button>
        </div>
        <div class="table">
            <table  width="100%" cellpadding="40px">
                <thead>
                <tr>
                    <th>Id</th>
                    <th>Title</th>
                    <th>Url</th>
                    <th>Date Created</th>
                    <th>Modified</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(post, index) in posts" v-bind:key="post._id">
                    <td>{{index}}</td>
                    <router-link
                            :to="{path: 'edit-posts', query: {id: post.route}}"
                    >{{post.title}}</router-link>
                    <td>{{post.route}}</td>
                    <td>Date</td>
                    <td>Modified</td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
    </div>
</template>

<script>
    import BlogService from "../RoutesService";

    export default {
        name: "blog.all",
        data() {
            return {
                posts: '',
            }
        },
        async created() {
            try {
                this.posts = await BlogService.getRoutes();

            } catch (err) {
                this.error = err.message;
            }
        },
    }
</script>

