<template>
  <div class="dash__container">
    <div>
        <div class="table_add__container">
            <Error v-if="error" :msg="error" />
            <div class="row">
                <div class="card mx-auto">
                    <div class="options_container">
                        <h1>New Restaurants</h1>
                        <div>
                            <button v-on:click="createRestaurant()">
                                Save
                            </button>
                            <button>
                                <router-link to="restaurants">
                                    Back
                                </router-link>
                            </button>

                        </div>
                    </div>
                    <div class="card-body">
                        <form>
                            <div class="form-group">
                                <h2>Name:</h2>
                                <input type="text"
                                       name="name"
                                       v-model="restaurantInfo.name"
                                       id="name"
                                >
                            </div>
                            <div class="form-group">
                                <h2>Address:</h2>
                                <input type="text"
                                       name="address"
                                       v-model="restaurantInfo.address"
                                       id="address"
                                >
                            </div>

                            <div class="form-group">
                                <h2>Number:</h2>
                                <input type="text"
                                       name="email"
                                       v-model="restaurantInfo.number"
                                       id="number"
                                >
                            </div>
                            <div v-for="(day, index) in days"
                                 v-bind:key="index">
                                <TimeForm :day="index" :info="day"/>
                            </div>
                            <div class="form-group">
                                <h2>Coordinates:</h2>
                                <input type="text"
                                       name="Coordinates"
                                       v-model="restaurantInfo.geometry.coordinates"
                                       id="coordinates"
                                >
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
  </div>

</template>
<script>
    import Error from './Error.vue'
    import TimeForm from './TimeForm.vue'
    import RestaurantService from "../RestaurantService";
export default {
    name: 'Restaurants',
    data() {
        return {
            newRestaurant: {

            },
            restaurantInfo: {
                name: '',
                address: '',
                number: '',
                geometry: {
                    "type": "Point",
                    "coordinates": ''
                },
            },
            days: {
                Monday: {
                    "hours": [{
                        "to": '',
                        "from": ''
                    }],
                    "time": ''
                },
                Tuesday: {
                    "hours": [{
                        "to": '',
                        "from": ''
                    }],
                    "time": ''
                },
                Wednesday: {
                    "hours": [{
                        "to": '',
                        "from": ''
                    }],
                    "time": ''
                },
                Thursday: {
                    "hours": [{
                        "to": '',
                        "from": ''
                    }],
                    "time": ''
                },
                Friday: {
                    "hours": [{
                        "to": '',
                        "from": ''
                    }],
                    "time": ''
                },
                Saturday: {
                    "hours": [{
                        "to": '',
                        "from": ''
                    }],
                    "time": ''
                },
                Sunday: {
                    "hours": [{
                        "to": '',
                        "from": ''
                    }],
                    "time": ''
                },
            },
            error: ''
        }
    },
    components: {
        Error,
        TimeForm
    },
    methods: {
        async createRestaurant() {
            this.cleanData();
            RestaurantService.insertRestaurant(this.newRestaurant).then(res => {
                if (res.data.success) {
                    this.$router.push('restaurants')
                }
            }).catch(err => {
                console.log(err)
            })
        },
        cleanData() {
            this.restaurantInfo.geometry.coordinates = this.restaurantInfo.geometry.coordinates.split(",");
            let coords = this.restaurantInfo.geometry.coordinates;
            coords.forEach( function (element, index) {
                coords[index] = parseFloat(element);
            });
            this.restaurantInfo.geometry.coordinates = coords;
            this.newRestaurant = {...this.restaurantInfo, ...this.days }

        }
    },

}

</script>

