<template>

    <div class="google-map_container">
        <div class="google-map" ref="googleMap"></div>
    </div>

</template>

<script>
    import MarkerClusterer from '@google/markerclusterer';
    import gmapsInit from '../utils/gmaps';
    import KeyService from "../KeyService";

    import _map from 'lodash/map'

    export default {
        name: 'App',
        props: ['allAddresses', 'allCoords', 'position'],
        data() {
            return {
                latLng: [],
                google: '',
                address: '',
                map: '',
            }
        },
        watch: {
          allAddresses: async function () {
              // let newCoords = this.position.split(",");
              // set map center to enter location

              const key = await KeyService.getKeys();
              const api_key = key[0].key;
              let google = await gmapsInit(api_key);
              this.google = google;
              let map  = new google.maps.Map(this.$refs.googleMap);
              this.map = map;
              const geocoder = new google.maps.Geocoder();
              geocoder.geocode({ address: this.allAddresses[0]}, (results, status) => {
                  if (status !== 'OK' || !results[0]) {
                      throw new Error(status);
                  }

                  map.setCenter(results[0].geometry.location);
                  map.fitBounds(results[0].geometry.viewport);
                  map.setZoom(12);


                  let marker = new google.maps.Marker({
                      // The below line is equivalent to writing:
                      // position: new google.maps.LatLng(-34.397, 150.644)
                      position: {lat: -34.397, lng: 150.644},
                      map: map
                  });

                  new MarkerClusterer(map, marker,
                      {imagePath: 'https://developers.google.com/maps/documentation/javascript/examples/markerclusterer/m'});
              });

          }
        },
    }
</script>

<style>
    html,
    body {
        margin: 0;
        padding: 0;
    }
</style>
