<template>
    <div class="dash__container">
        <div>
            <div class="table_add__container">
                <Error v-if="error" :msg="error" />
                <div class="row">
                    <div class="card mx-auto">
                        <div class="options_container">
                            <h1>Edit User</h1>
                            <div>
                                <button v-on:click="registerUser()">
                                    Save
                                </button>
                                <button>
                                    <router-link to="users">
                                        Back
                                    </router-link>
                                </button>
                            </div>
                        </div>
                        <div class="card-body">
                            <form>
                                <div class="form-group">
                                    <h2>Name:</h2>
                                    <input type="text"
                                           name="name"
                                           v-model="name"
                                           id="name"
                                    >
                                </div>
                                <div class="form-group">
                                    <h2>Username:</h2>
                                    <input type="username"
                                           name="route"
                                           v-model="username"
                                           id="username"
                                    >
                                </div>
                                <div class="form-group">
                                    <h2>Email:</h2>
                                    <input type="text"
                                           name="email"
                                           v-model="email"
                                           id="email"
                                    >
                                </div>
                                <div class="form-group">
                                    <h2>New Password:</h2>
                                    <input type="text"
                                           name="password"
                                           v-model="password"
                                           id="password"
                                    >
                                </div>
                                <div class="form-group">
                                    <h2>Password Confirmation:</h2>
                                    <input type="text"
                                           name="confirm_password"
                                           v-model="confirm_password"
                                           id="confirm_password"
                                    >
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>

<script>
    import {mapActions, mapGetters} from 'vuex';
    import Error from './Error.vue'

    export default {
        name: "NewUser",
        data() {
            return {
                username: '',
                password: '',
                name: '',
                email: '',
                confirm_password: ''
            }
        },
        components: {
            Error
        },
        methods: {
            ...mapActions(['register']),
            registerUser() {
                let user = {
                    username: this.username,
                    name: this.name,
                    password: this.password,
                    confirm_password: this.confirm_password,
                    email: this.email
                }
                this.register(user).then(res => {
                    if (res.data.success) {
                        this.$router.push('/admin/users')
                    }
                }).catch(err => {
                    console.log(err)
                })

            }
        },
        computed: {
            ...mapGetters(['error'])
        }
    }
</script>

