<template>
    <div class="dash__container">
        <div>
            <div class="table_add__container">
                <Error v-if="error" :msg="error" />
                <div class="row">
                    <div class="card mx-auto">
                        <div class="options_container">
                            <h1>Edit User</h1>
                            <div>
                                <button v-on:click="update()">
                                    Save
                                </button>
                                <button>
                                    <router-link to="users">
                                        Back
                                    </router-link>
                                </button>
                                <button v-on:click="removeUser()">
                                    Delete
                                </button>
                            </div>
                        </div>
                        <div v-if="user.length !== 0" class="card-body">
                            <form>
                                <div class="form-group">
                                    <h2>Name:</h2>
                                    <input type="text"
                                           name="name"
                                           v-model="user.name"
                                           id="name"
                                    >
                                </div>
                                <div class="form-group">
                                    <h2>Username:</h2>
                                    <input type="username"
                                           name="route"
                                           v-model="user.username"
                                           id="username"
                                    >
                                </div>
                                <div class="form-group">
                                    <h2>Email:</h2>
                                    <input type="text"
                                           name="email"
                                           v-model="user.email"
                                           id="email"
                                    >
                                </div>
                                <div class="form-group">
                                    <h2>Current Password:</h2>
                                    <input type="text"
                                           name="current-password"
                                           v-model="user.current"
                                           id="current-password"
                                    >
                                </div>
                                <div class="form-group">
                                    <h2>New Password:</h2>
                                    <input type="text"
                                           name="date"
                                           v-model="user.new"
                                           id="date"
                                    >
                                </div>
                                <div class="form-group">
                                    <h2>Password Confirmation:</h2>
                                    <input type="text"
                                           name="image"
                                           v-model="user.confirm"
                                           id="image"
                                    >
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>

<script>
    import Error from "../components/Error.vue"
    import {mapActions} from 'vuex';
    export default {
        name: "EditUser",
        data() {
            return {
                user: '',
                error: ''
            }
        },
        components: {
            Error
        },
        props: [
            'id'
        ],
        methods: {
            ...mapActions(['getUser', 'deleteUser', 'updateUser']),
            removeUser() {
                try {
                    this.deleteUser(this.user._id).then(res => {
                        console.log(res)
                        if (res.success) {
                            this.$router.push('/admin/users')
                        }
                    }).catch(err => {
                        console.log(err.msg)
                    })
                } catch (err) {
                    this.error = err.message;
                }
            },
            update() {
                this.updateUser(this.user._id, this.user).then(res => {
                    if (res.data.success) {
                        this.$router.push('/admin/users')
                    }
                }).catch(err => {
                    console.log(err)
                })
            }

        },
        async created() {
            try {
                this.user = await this.getUser(this.id);
            } catch (err) {
                this.error = err.message;
            }
        },
    }
</script>

