<template>
    <div class="dash__container">
        <div class="row">
            <div class="card mx-auto">
                <div class="options_container">
                    <h1>Edit Restaurant</h1>
                    <div>
                        <button v-on:click="updateRestaurant()">
                            Save
                        </button>
                        <button>
                            <router-link to="restaurants">
                                Back
                            </router-link>
                        </button>
                        <button v-on:click="deleteRestaurant()">
                            Delete
                        </button>
                    </div>
                </div>
                <div v-if="restaurant.length !== 0" class="card-body">
                    <form>
                        <div class="form-group">
                            <h2>Name:</h2>
                            <input type="text"
                                   name="name"
                                   v-model="restaurant.name"
                                   id="name"
                            >
                        </div>
                        <div class="form-group">
                            <h2>Address:</h2>
                            <input type="text"
                                   name="address"
                                   v-model="restaurant.address"
                                   id="address"
                            >
                        </div>

                        <div class="form-group">
                            <h2>Number:</h2>
                            <input type="text"
                                   name="email"
                                   v-model="restaurant.number"
                                   id="number"
                            >
                        </div>
                        <TimeForm day="Monday" :info="restaurant.Monday"/>
                        <TimeForm day="Tuesday" :info="restaurant.Tuesday"/>
                        <TimeForm day="Wednesday" :info="restaurant.Wednesday"/>
                        <TimeForm day="Thursday" :info="restaurant.Thursday"/>
                        <TimeForm day="Friday" :info="restaurant.Friday"/>
                        <TimeForm day="Saturday" :info="restaurant.Saturday"/>
                        <TimeForm day="Sunday" :info="restaurant.Sunday"/>


                        <div class="form-group">
                            <h2>Coordinates:</h2>
                            <input type="text"
                                   name="Coordinates"
                                   v-model="coordinates"
                                   id="coordinates"
                            >
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import RestaurantService from "../RestaurantService";
    import TimeForm from './TimeForm.vue';

    export default {
        name: "EditRestaurant",
        props: [
            'id'
        ],
        components: {
          TimeForm
        },
        data() {
            return {
                restaurant: '',
                coordinates: ''
            }
        },
        methods: {
            async deleteRestaurant() {
                try {
                    await RestaurantService.deleteRestaurant(this.id).then(res => {
                        if (res.data.success) {
                            this.$router.push('/admin/restaurants')
                        }
                    }).catch(err => {
                        console.log(err.response.data.msg)
                    })
                } catch (err) {
                    this.error = err.message;
                }
            },
            async updateRestaurant() {
                try {
                    await RestaurantService.updateRestaurant(this.id, this.restaurant).then(res => {
                        if (res.data.success) {
                            this.$router.push('/admin/restaurants')
                        }
                    }).catch(err => {
                        console.log(err.response.data.msg)
                    })
                } catch (err) {
                    this.error = err.message;
                }
            },
        },
        async created() {
            try {
                this.restaurant = await RestaurantService.getOne(this.id);
                this.coordinates = this.restaurant.geometry.coordinates[0] + ',' + this.restaurant.geometry.coordinates[1]

            } catch (err) {
                this.error = err.message;
            }
        },
    }
</script>

