import Vue from 'vue'
import Router from 'vue-router'
import Restaurant from '../components/Restaurants.vue'
import Results from '../components/Results.vue'
import Welcome from '../components/Welcome.vue'
import Explore from '../components/Explore.vue'
import About from '../components/About.vue'
import EditorPicks from '../components/Random.vue'
import Login from '../components/Login.vue'
import Dashboard from '../components/Dashboard.vue'
import Register from '../components/Register.vue'
import store from '../storage/index.js'
import Profile from "../components/Profile.vue"
import Admin from "../components/Admin.vue"
import Restaurants from '../components/All_Restaurants.vue'
import NewRestaurants from '../components/Restaurants.vue'
import EditRestaurant from '../components/EditRestaurant.vue'
import Users from "../components/Users.vue"
import Blog from '../components/BlogAll.vue'
import newBlog from '../components/NewBlog.vue'
import editBlog from '../components/EditBlog.vue'
import editUsers from '../components/EditUser.vue'
import newUsers from '../components/NewUser.vue'
import Contact from '../components/Contact.vue'

Vue.use(Router)

const router =  new Router( {
    routes: [
        {
            path: '/',
            component: Welcome,
            meta: {
                title: '5to7 | Find Your Perfect Happy Hour',
                metaTags: [
                    {
                        name: 'description',
                        content: 'Find Your Perfect Happy Hour.'
                    },
                    {
                        property: 'og:description',
                        content: 'Find Your Perfect Happy Hour.'
                    }
                ]
            }
        },
        {
            path: '/contact',
            component: Contact,
            meta: {
                title: '5to7 | Contact Us',
                metaTags: [
                    {
                        name: 'description',
                        content: 'Find Your Perfect Happy Hour.'
                    },
                    {
                        property: 'og:description',
                        content: 'Find Your Perfect Happy Hour.'
                    }
                ]
            }
        },
        {
            path: '/results',
            component: Results,
            props(route) {
                return  route.query || {}
            }
        },
        {
            path: '/restaurant',
            component: Restaurant
        },
        {
            path: '/about',
            component: About
        },
        {
            path: '/explore',
            component: Explore,
            meta: {
                title: '5to7 | Discover Recently Added Happy Hour',
                metaTags: [
                    {
                        name: 'description',
                        content: 'Discover Recently Added Happy Hour.'
                    },
                    {
                        property: 'og:description',
                        content: 'Discover Recently Added Happy Hour.'
                    }
                ]
            }
        },
        {
            path: '/random',
            component: EditorPicks
        },
        {
            path: '/login',
            component: Login,
            meta: {
                requiresGuest: true
            }
        },
        {
            path: '/admin',
            component: Admin,
            meta: {
                requiresAuth: true
            },
            children: [
                {
                    path: 'profile',
                    name: 'profile',
                    component: Profile,
                    meta: {
                        requiresAuth: true
                    },
                },
                {
                    path: 'dashboard',
                    name: 'dashboard',
                    component: Dashboard,
                    meta: {
                        requiresAuth: true
                    },
                },
                {
                    path: 'restaurants',
                    name: 'restaurants.all',
                    component: Restaurants,
                    meta: {
                        requiresAuth: true
                    },
                },
                {
                    path: 'new-restaurants',
                    name: 'restaurants.new',
                    component: NewRestaurants,
                    meta: {
                        requiresAuth: true
                    },
                },
                {
                    path: 'edit-restaurants',
                    name: 'restaurants.edit',
                    component: EditRestaurant,
                    props(route) {
                        return  route.query || {}
                    },
                    meta: {
                        requiresAuth: true
                    },
                },
                {
                    path: 'users',
                    name: 'users.all',
                    component: Users,
                    meta: {
                        requiresAuth: true
                    },
                },
                {
                    path: 'blog-posts',
                    name: 'blog.all',
                    component: Blog,
                    meta: {
                        requiresAuth: true
                    },
                },
                {
                    path: 'new-posts',
                    name: 'newBlog',
                    component: newBlog,
                    meta: {
                        requiresAuth: true
                    },
                },
                {
                    path: 'edit-posts',
                    name: 'editBlog',
                    component: editBlog,
                    props(route) {
                        return  route.query || {}
                    },
                    meta: {
                        requiresAuth: true
                    },
                },
                {
                    path: 'edit-users',
                    name: 'editUsers',
                    component: editUsers,
                    props(route) {
                        return  route.query || {}
                    },
                    meta: {
                        requiresAuth: true
                    },
                },
                {
                    path: 'new-users',
                    name: 'NewUsers',
                    component: newUsers,
                    props(route) {
                        return  route.query || {}
                    },
                    meta: {
                        requiresAuth: true
                    },
                },
            ],
        },
        {
            path: '/register',
            component: Register,
            meta: {
                requiresGuest: true
            }
        },

    ],
    mode: 'history'
    }
)


router.beforeEach((to, from, next) => {
    document.title = to.meta.title
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (!store.getters.isLoggedIn) {
            //Redirect to the Login Page
            next('/login');
        }
        else {
            next();
        }
    }
    else if (to.matched.some(record => record.meta.requiresGuest)) {
        if (store.getters.isLoggedIn) {
            //Redirect to the Dashboard Page
            next('/admin/dashboard');
        }
        else {
            next();
        }
    }
    else {
        next();
    }
})

export default router
