<template>
    <div class="dash__container">
        <div class="options_container">
            <h1>Manage Users</h1>
            <router-link to="new-users">
            <button>Add user</button>
            </router-link>
        </div>
        <div class="table">
            <table  width="100%" cellpadding="40px">
                <thead>
                <tr>
                    <th>Id</th>
                    <th>Name</th>
                    <th>Email</th>
                    <th>Username</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(user, index) in users" v-bind:key="user._id">
                    <td>{{index}}</td>
                    <td><router-link
                            :to="{path: 'edit-users', query: {id: user._id}}"
                    >{{user.name}}</router-link></td>
                    <td>{{user.email}}</td>
                    <td>{{user.username}}</td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
    import {mapActions} from 'vuex';
    export default {
        name: "Users",
        data() {
            return {
                users: ''
            }
        },
        computed: {
        },
        methods: {
            ...mapActions(['getUsers'])
        },
        async mounted() {
            this.users = await this.getUsers();
        }
    }
</script>

