<template>
<div class="main-results-container">
  <Search  @clicked="updateCoord"/>
    <div class="mobile-filters-container" :class="accordionClasses" v-on:click="toggleAccordion">
        <h2>Filters</h2>
        <Filters class="tablet-filter-body" />

    </div>
    <h1>{{day}} at {{time}} in {{location}}</h1>

    <div class="restaurant-container">
        <div class="filters_coontainer">
            <h2>Filters</h2>
            <Filters class="desktop" />

        </div>
      <div class="all_restaurants">
      <div v-if="restaurants.length > 0">
      <div class="restaurant-card" v-for="(restaurant, index) in restaurants"
           v-bind:key="index">
          <span class="restaurant-number">{{index + 1}}</span>

        <div class="restaurant-details">
            <div class="title-container">
                <h3>{{restaurant.name}}</h3>
                <h4>Food Style</h4>
            </div>
            <div class="details-container">
                <div class="hours-container"> <span><img src="../assets/clock_icon.svg"/></span>
                    <ul>
                        <li>
                     {{restaurant[day].time}}
                        </li>
                    </ul>
                </div>
                <p>Try the Featured Drink</p>
            </div>
            <div class="details-button-container">
                <span class="details-button">
                View Details
                </span>
            </div>
        </div>

          <div class="images" v-on:click="show()">
              <img src="../assets/example_restant.png">
          </div>
      </div>
      </div>
      <div v-else>
        There are no restaurants
      </div>
    </div>
        <vue-modaltor :visible="modal"
                      @hide="show()"
                      defaultWidth="50%"
                      class="restaurant-specifics_wrapper"

        >   <div class="restaurant-specifics_container">
            <section class="restaurant_main">
                <h3>Restaurant Name</h3>
                <div class="details-container">
                    <div class="hours-container">
                        <ul>
                            <li><img src="../assets/clock_icon.svg"/></li>
                            <li>
                                5-7pm
                            </li>
                        </ul>
                    </div>
                    <span class="modal-details-button">Food Style</span>
                </div>
                <div class="images-container">
                    <carousel :autoplay="true"
                              :loop="true"
                              :navigationEnabled="true"
                              :autoplayTimeout='10000'
                              :per-page="1">
                        <slide>
                            <img src="../assets/martini_placeholder.png">
                        </slide>
                        <slide>
                            <img src="../assets/menu.jpg" height="100%" width="300px" scrolling="auto" />
                        </slide>
                    </carousel>
                </div>
            </section>
            <section class="restaurant_details">
                <div class="detail">
                    <img src="../assets/website.svg"/><span>www.websitename.com</span>
                </div>
                <div class="detail">
                    <img src="../assets/phone.svg"/><span>(202) 379-5741</span>
                </div>
                <div class="detail">
                    <img src="../assets/small_location.svg"/><span>3837 Arlington Ave #1/2, <br>
                        Los Angeles, CA
                    </span>
                </div>

                <div class="google-map_container">
                <div ref="googleMap" id="map"></div>
            </div>
            </section>
        </div>

        </vue-modaltor>

        <GoogleMapLoader
            :allAddresses="allAddresses"
            :allCoords="allCoords"
            :position="position"
    >

    </GoogleMapLoader>
  </div>
</div>
</template>

<script>
    import Search from './Search.vue';
    import RestaurantService from "../RestaurantService";
    import GoogleMapLoader from './GoogleMapLoader.vue'
    import Filters from './Filters.vue'
    import gmapsInit from '../utils/gmaps';
    import KeyService from "../KeyService";
    import Vue from "vue";
    import VueModalTor from "vue-modaltor";
    import { Carousel, Slide } from 'vue-carousel';


    Vue.use(VueModalTor);

    export default {
        props: [
          'position',
            'time',
            'location'
        ],
        name: "Results",
        components:{
            GoogleMapLoader,
            Search,
            Filters
        },
        data() {
            return {
                address: '',
                allAddresses: [],
                allCoords: [],
                coord: [-118.373253,34.16486],
                restaurants: [],
                day: '',
                hours: [],
                times: [],
                modal: false,
                google: '',
                mapAddress: '',
                map: '',
                isOpen: false
            }
        },
        methods: {
            async loadMap() {

                const key = await KeyService.getKeys();
                const api_key = key[0].key;
                let google = await gmapsInit(api_key);
                this.google = google;
                let map = new google.maps.Map(document.getElementById("map"), {
                    center: { lat: -34.397, lng: 150.644 },
                    zoom: 8,
                    mapTypeId: this.google.maps.MapTypeId.ROADMAP
                });
                let marker = new google.maps.Marker({
                    map: map,
                    draggable: true,
                    animation: google.maps.Animation.DROP,
                    position: { lat: -34.397, lng: 150.644 }

                })



            },
            show() {
                this.modal = !this.modal;
                this.loadMap();
            },
            hide() {

            },
            formatNumber (number) {
                var cleaned = ('' + number).replace(/\D/g, '')
                var match = cleaned.match(/^(\d{3})(\d{3})(\d{4})$/)
                if (match) {
                    return '(' + match[1] + ') ' + match[2] + '-' + match[3]
                }
            },
          updateCoord (coord) {
              this.restaurants = [];
              this.allAddresses = [];
              this.allCoords = [];
              this.restaurants = coord;
              this.address = coord[0].address;
              coord.forEach(restaurant => {
                  this.allAddresses.push(restaurant.address);
                  this.allCoords.push(restaurant.position);
              });
          },
            getDay () {
                var d = new Date();
                var weekday = new Array(7);
                weekday[0] = "Sunday";
                weekday[1] = "Monday";
                weekday[2] = "Tuesday";
                weekday[3] = "Wednesday";
                weekday[4] = "Thursday";
                weekday[5] = "Friday";
                weekday[6] = "Saturday";
                var n = weekday[d.getDay()];
                this.day = n;
                this.restaurants.forEach(element => this.hours.push(element[n]));
                var i;
                var x;
                for (i = 0; i < this.hours.length; i++) {
                    let openings = this.hours[i];
                    let b = [];
                    for (x = 0; x < openings.length; x++) {
                        let a = {};
                        let open = openings[x];
                        a.to = this.convertMins(open.to);
                        a.from = this.convertMins(open.from);
                        a.id = x;
                        if (a.to === '0:00 AM') {
                            a.to = 'All Day';
                            a.from = '';
                        }
                        b.push(a);
                    }
                    this.times.push(b);
                }

            },
            convertMins(time) {

                let end;
                let opening;
                if (time > 1440) {
                    time = time - 1440;
                }
                let hours = Math.floor(time / 60);
                if (hours > 11) {
                    end = 'PM';
                    hours = hours - 12;
                }
                else {
                    end = 'AM';
                }
                let min = (time % 60);
                if (min < 10) {
                    opening = hours + ':0' + min + ' ' + end
                }
                else {
                    opening = hours + ':' + min + ' ' + end
                }
                return opening
            },
            toggleAccordion: function() {
                this.isOpen = !this.isOpen;
            }
        },
        computed: {
            mapConfig () {
                return {
                    center: { lat: 0, lng: 0 }
                }
            },
            accordionClasses: function() {
                return {
                    'is-closed': !this.isOpen,
                    'is-primary': this.isOpen,
                    'is-dark': !this.isOpen
                };
            }
        },
        async created() {
            try {
                this.restaurants = await RestaurantService.getCloseRestaurants(this.position, this.time);
                this.getDay();
                this.address = this.restaurants[0].address
                this.restaurants.forEach(restaurant => {
                    this.allAddresses.push(restaurant.address);
                    this.allCoords.push(restaurant.position);
                });


            } catch (err) {
                this.error = err.message;
            }
        },
    }
</script>

