<template>
    <div class="form-group" :id="day">
        <h2>{{day}}:</h2>
        <div v-for="(hour, index) in info.hours"
             v-bind:key="index"
             class="time-input__container"
        >
            <input class="time-inputs"
                   type="number"
                   placeholder="start"
                   v-model.number="hour.to"
            >
            <p>to</p>
            <input class="time-inputs"
                   type="number"
                   placeholder="end"
                   v-model.number="hour.from"
            >
        </div>
        <img src="https://img.icons8.com/ios/64/000000/add.png" v-on:click="addHours()">
        <input type="text"
               v-model="info.time"
        >
    </div>

</template>

<script>
    export default {
        name: "TimeForm",
        props: ['day', 'info'],
        data() {
            return {
            }
        },
        methods: {
            addHours () {

                let time = {
                        "to": '',
                        "from": ''
                    }
                this.info.hours.push(time)
            }
        }
    }
</script>

