<template>
    <div class="dash__container">
        <div>
            <div class="table_add__container">
                <Error v-if="error" :msg="error" />
                    <div class="row">
                        <div class="card mx-auto">
                            <div class="options_container">
                                <h1>Edit Blog Post</h1>
                                <div>
                                    <button v-on:click="updatePost()">
                                        Save
                                    </button>
                                    <button>
                                        <router-link to="blog-posts">
                                            Back
                                        </router-link>
                                    </button>
                                    <button v-on:click="deletePost()">
                                        Delete
                                    </button>
                                </div>
                            </div>
                            <div v-if="blogData.length !== 0" class="card-body">
                                <form>
                                    <div class="form-group">
                                        <h2>Blog Title:</h2>
                                        <input type="text"
                                               name="title"
                                               v-model="blogData.title"
                                               id="title"
                                        >
                                    </div>
                                    <div class="form-group">
                                        <h2>Url:</h2>
                                        <input type="text"
                                               name="route"
                                               v-model="blogData.route"
                                               id="route"
                                        >
                                    </div>
                                    <div class="form-group">
                                        <h2>Restaurant:</h2>
                                        <input type="text"
                                               name="restaurant"
                                               v-model="blogData.restaurant"
                                               id="restaurant"
                                        >
                                    </div>
                                    <div class="form-group">
                                        <h2>Author:</h2>
                                        <input type="text"
                                               name="name"
                                               v-model="blogData.name"
                                               id="name"
                                        >
                                    </div>
                                    <div class="form-group">
                                        <h2>Date:</h2>
                                        <input type="text"
                                               name="date"
                                               v-model="blogData.date"
                                               id="date"
                                        >
                                    </div>
                                    <div class="form-group">
                                        <h2>Image:</h2>
                                        <input type="text"
                                               name="image"
                                               v-model="blogData.image"
                                               id="image"
                                        >
                                    </div>
                                    <div class="form-group">
                                        <h2>Content:</h2>
                                        <textarea
                                                name="content"
                                                v-model="blogData.content"
                                                id="content"
                                                rows="20" cols="50"
                                        >
                                        </textarea>
                                    </div>
                                    <hr>
                                    <div class="form-group">
                                        <h2>Meta Title:</h2>
                                        <input  type="text"
                                                name="meta_title"
                                                v-model="blogData.meta.title"
                                                id="meta_title"
                                        >
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
            </div>
        </div>
    </div>
</template>

<script>
    import PostService from "../RoutesService";
    import Error from "../components/Error.vue"
    export default {
        name: "EditBlog",
        data() {
            return {
                blogData: '',
                error: ''
            }
        },
        components: {
          Error
        },
        props: [
            'id'
        ],
        methods: {
            deletePost() {
                try {
                    PostService.deletePost(this.blogData._id).then(res => {
                        if (res.data.success) {
                            this.$router.push('/admin/blog-posts')
                        }
                    }).catch(err => {
                        console.log(err.response.data.msg)
                    })
                } catch (err) {
                    this.error = err.message;
                }
            },
            updatePost() {
                try {
                    PostService.updatePost(this.blogData._id, this.blogData).then(res => {
                        if (res.data.success) {
                            this.$router.push('/admin/blog-posts')
                        }
                    }).catch(err => {
                        console.log(err.response.data.msg)
                    })
                } catch (err) {
                    this.error = err.message;
                }
            },
        },
        async created() {
            try {
                let blogData = await PostService.getOneRoute(this.id);
                this.blogData = blogData[0];
            } catch (err) {
                this.error = err.message;
            }
        },
    }
</script>

